import xlistInput from '../../../wxcomponents/xlist/xlist-input.vue'
import getcodec from '../../../wxcomponents/getcode/getcodec.vue'

export default {
	data() {
		return {
			isDefault: false,
			phoneCode:'',
			scode:'',
			orderNum:'',
			buttonTitle: '保存',
			fkey:'',
			skey:'',
			phone: ''
		}
	},
	methods: {
		bindScode(e){
			this.scode = e.scode
		},
		bindPhoneCode(e){
			this.phoneCode = e.phoneCode
		},
		bindDefault(e){
			this.phone = e
		},
		bindInput(e){
			this.fkey = e
		},
		bindSecond(e){
			this.skey = e
		},
		onNext() {
			let skey = this.skey
			let fkey = this.fkey
			if( skey !== fkey){
				uni.showToast({
					title: '两次密码输入不一致',
					icon:'none'
				})
			}else if(this.phoneCode !== this.scode){
				uni.showToast({
					title: '验证码不正确',
					icon: 'none'
				})
			} else {
				this.onReg(skey)
			}
		},
		onReg(e){
			let reg =/^(?!([a-zA-Z]+|\d+)$)[a-zA-Z\d]{6}$/
			let str = e
			if(str.match(reg) == null){
				uni.showToast({
					title: '密码格式不正确',
					icon:'none'
				})
			} else{
				this.bindSubmit()
			}
		},
		bindSubmit(){
			let phoneCode = this.phoneCode.toString()
			let fkey = this.fkey.toString()
			if(fkey == this.skey){
				let phone = this.phone.toString()
				uni.request({
					url: 'https://www.finetwm.com/xmRepair/shopInfo/resetPassword',
					method: 'POST',
					header:{
						"content-Type": "application/x-www-form-urlencoded"
					},
					data: {
						phone: phone,
						password: fkey
					},
					success: res => {
						console.log(res)
						uni.showToast({
							title: '保存成功'
						})
						setTimeout(() => {
							uni.hideToast()
							uni.navigateBack({
								delta: 1
							})
						}, 1000)
						
					},
					fail: err => {
						console.log(err)
					}
				})
			} else {
				uni.showToast({
					title: '验证码输入不正确',
					icon: 'none'
				})
			}
		}
	},
	components:{
		xlistInput,
		getcodec
	}
}
